<template>
    <div :style="elementStyle">
      <div style="object-fit: cover;">
        <svg-icon :icon-class="iconSrc" :style="iconStyle" />
      </div>
      <div :style="textStyle" @click="handleFileNameClick">{{ fileName }}</div>
    </div>
  </template>
  
  <script>
  // 引入常量
  import { ICON } from "@/entity/_Common/Icon";
  export default {
    // props: ["fileName", "fileType", "iconStyle", "textStyle"],
    props: {
      fileName: {
        type: String,
        default: "",
      },
      fileType: {
        type: String,
        default: "",
      },
      elementStyle: {
        // 整体组件样式
        type: String,
        default: "display: flex;align-items: center;",
      },
      iconStyle: {
        // 图标样式
        type: String,
        default: "",
      },
      textStyle: {
        // 文字样式
        type: String,
        default: "",
      },
    },
    components: {},
    computed: {
      iconSrc() {
        for (let i in ICON) {
          for (let j in ICON[i].extension) {
            if (this.fileType === ICON[i].extension[j]) {
              return ICON[i].iconSrc;
            }
          }
        }
        return "other-file-preview";
      },
    },
    data() {
      return {};
    },
    async mounted() {},
    methods: {
      handleFileNameClick() {
        this.$emit('filename-click')
      },
    },
  };
  </script>
  
  <style lang="scss" scoped></style>